@import 'imports/vars.scss';

.link-append,
.answer {
	
	width: 70px;
	height: 30px;
	background-color: $color;
	border-radius: 5px;
	border: 1px solid transparent;
 	font-size: 12px;
   	color: $color-white;
    padding: 4px 0;
    margin: 15px 0 15px auto;
	text-align: center;
	font-weight: normal;
}

.link-append {

	background-color: transparent;
	border: 1px solid $color-white;
	margin: 0 0 0 auto;
}


.title-commits {

	display: inline-flex;
	width: 100%;
	background-color: $color;
	font-family: $font-serif;
	font-weight: 700;
	color: $color-white;
	padding: 10px 20px;
	margin-bottom: 25px;

	.count { 

		padding: 0 10px;
		opacity: 0.8;
	}
}

input[type="submit"]:hover,
.answer:hover  { 
		
		background-color: transparent;
		border: 1px solid $color;
		color: $color; 
	}

ul.ul-commits { padding: 0;}

ul.ul-commits  li { display: block;

	&:before { content: none;}

	  ul { 

	  	@media screen and (max-width: 1023px) { padding-left: 10px; }

	    li:before {

			content: "\f105";        
        	display: inline-block;
	        font: 14px $icomoon;
	        color: $color;
	        padding: 15px 0 0 0;
	        margin-left: 0;
	        margin-right: 20px;
	        vertical-align: top;
    	}

    	.text-commit {  padding: 10px 10px 10px 20px; }
     
        ul { padding-left: 60px;

        	@media screen and (max-width: 1023px) { padding-left: 20px; }

	       > li:before {
	        	
	        	content: "\f105 \f105";    
		        display: inline-block;
		        font: 14px $icomoon;
	       		color: $color;
		        vertical-align: top;
		        letter-spacing: 4px;
	    	}
	    }
    }   
}

.commit-item span { display: inline-block; }

.icon {

	width: 32px;
 	height: 32px;
  	background-image: url(/images/user.png);
  	background-repeat: no-repeat;
  	background-size: contain;
  	margin-right: 20px;
}

.time {

  color: $color;
  font-size: 14px;
}

.text-commit { 

	border-bottom: 1px solid #f9faff;
	font-size: 14px;
	line-height: 1.71;
    padding: 10px 10px 0 0;
	margin-bottom: 20px;
}

input[type="submit"] {

	background-color: $color; 
	border: 1px solid transparent;
	margin: 15px 0 15px auto;
	padding: 4px 0;
}

.comment-reply-title { 

	font-weight: 700;
	padding: 0 0 15px 0;
}
 
.comment-form { 

	background-color: #f9faff;
	padding: 20px 40px;

	* {
		border-radius: 5px;
		border: 0;
		background-color: $color-white;
	}

	input[type="text"] { 

		flex-basis:calc(100%/2 - 30px);
		padding: 10px;
		margin: 15px 0;

		@media screen and (max-width: 1023px) { flex-basis: 100%; }
	}

	input[type="submit"] { 
		
		width: 100%;
		color: $color-white;
		padding: 10px;
		cursor: pointer;
	}

	textarea { 

		width: 100%;
		height: 80px;
		padding: 10px;
	}

	input::-webkit-input-placeholder,
	textarea::-webkit-input-placeholder {
		
		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;
	}

	::-moz-placeholder       {

		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;
		opacity: .5;
	}

	:-ms-input-placeholder  {

		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;
		opacity: .5;
	}
}

